<template>
  <ValidationObserver ref="form" v-slot="{ handleSubmit }">
    <form class="mt-4" novalidate @submit.prevent="handleSubmit(onSubmit)">
      <ValidationProvider v-slot="{ errors }">
        <div class="form-group">
          <label for="emailInput">Username</label>
          <input type="text" :class="'form-control mb-0' +(errors.length > 0 ? ' is-invalid' : '')"
                 id="emailInput" aria-describedby="emailHelp" autocomplete
                 v-model="user.username" placeholder="Username" required>
          <div class="invalid-feedback">
            <span>{{ errors[0] }}</span>
          </div>
        </div>
      </ValidationProvider>
      <ValidationProvider vid="password" name="Password" rules="required" v-slot="{ errors }">
        <div class="form-group">
          <label for="passwordInput">Password</label>
          <a @click="btn" class="float-right">
            忘记密码 ？
          </a>
          <input type="password" :class="'form-control mb-0' +(errors.length > 0 ? ' is-invalid' : '')"
                 id="passwordInput" autocomplete
                 v-model="user.password" placeholder="Password" required>
          <div class="invalid-feedback">
            <span>{{ errors[0] }}</span>
          </div>
        </div>
      </ValidationProvider>
      <div class="d-inline-block w-100">
        <div class="custom-control custom-checkbox d-inline-block mt-2 pt-1">
          <input type="checkbox" class="custom-control-input" :id="formType">
          <label class="custom-control-label" :for="formType">记住我</label>
        </div>
        <button type="submit" class="btn btn-primary float-right">Sign in</button>
      </div>
      <div class="sign-info" style="display: none">
          <span class="dark-color d-inline-block line-height-2">
            Don't have an account?
            <router-link to="/auth/sign-up1" class="iq-waves-effect pr-4">
              Sign up
            </router-link>
          </span>
      </div>
    </form>
  </ValidationObserver>
</template>

<script>
import auth from '../../../../services/auth'
import Snackbar from 'node-snackbar'
import { mapGetters } from 'vuex'

export default {
  name: 'SignIn1Form',
  props: ['formType', 'username', 'password'],
  data: () => ({
    user: {
      username: '',
      password: ''
    }
  }),
  mounted () {
    this.user.username = this.$props.username
    this.user.password = this.$props.password
  },
  computed: {
    ...mapGetters({
      stateUsers: 'Setting/usersState'
    })
  },
  methods: {
    btn () {
      Snackbar.show({ text: '找不回了重新注册吧', backgroundColor: '#323232' })
    },
    onSubmit () {
      if (this.formType === 'jwt') {
        this.jwtLogin()
      } else {
        Snackbar.show({ text: '我也不知道是什么问题', backgroundColor: '#323232' })
      }
    },
    async jwtLogin () {
      const { data: res } = await auth.login(this.user)
      if (res.code === 200) {
        localStorage.setItem('user', JSON.stringify(res))
        localStorage.setItem('access_token', res.data)
        await this.$router.push({ name: 'dashboard.home-1' })
      }
      Snackbar.show({ text: res.msg, backgroundColor: '#323232' })
    }
  }
}
</script>
